/* slidetoggles */ 

.fold-toggle {
  cursor: pointer;

  &:before {
    content: "\f0d7";
    margin-right: 0.2em;
    font-family: "Font Awesome 5 Free";
  }
}

/* images overlay; */

.img-wrapper, .image-block-wrapper {
  border-radius: .3rem;
}

.image-block .image-block-outer-wrapper .image-block-wrapper{
  background: #ee7203;
}
.image-block .image-block-outer-wrapper img {
  transition: opacity .2s linear;
  -webkit-backface-visibility: hidden;
}

.row > .col.span-1:hover, .row > .col.span-2:hover, .row > .col.span-3:hover, .row > .col.span-4:hover, .row > .col.span-5:hover {

  > .image-block .image-block-outer-wrapper a img{
      opacity: 0.75;
  }
}

.image-block-outer-wrapper:hover a img{
  opacity: 0.75;
}

/* kontakt map hack helper */

.disable-hover {
  pointer-events: none;
}

    
